import React, { Component } from "react";
import Breadcrumb from "../common/breadcrumb";
import { connect } from "react-redux";
import { Siteurl } from "../../services/script";
import { Link } from "react-router-dom";
import { useHistory } from "react-router";

class BlogDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: {
        title: "",
      },
      next: {
        title: "",
      },
      previous: {
        title: "",
      },
    };
  }

  componentDidMount() {
    this.getcontentbyid(this.props.contentId);
  }

  getcontentbyid = (contentId) => {
    fetch(Siteurl + "service/getcontentbyid", {
      method: "POST",
      headers: {
        "X-Requested-With": "XMLHttpRequest",
      },
      body: JSON.stringify({ id: contentId }),
    })
      .then((res) => res.json())
      .then(
        (result) => {
          this.setState({ content: result.result });
          this.setState({ next: result.next });
          this.setState({ previous: result.previous });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error,
          });
        }
      );
  };

  getcontentnextid = (contentId) => {
    fetch(Siteurl + "service/getcontentbyid", {
      method: "POST",
      headers: {
        "X-Requested-With": "XMLHttpRequest",
      },
      body: JSON.stringify({ id: contentId }),
    })
      .then((res) => res.json())
      .then((result) => {
        this.setState({ contentnext: result.result });
      });
  };

  getcontentbackid = (contentId) => {
    fetch(Siteurl + "service/getcontentbyid", {
      method: "POST",
      headers: {
        "X-Requested-With": "XMLHttpRequest",
      },
      body: JSON.stringify({ id: contentId }),
    })
      .then((res) => res.json())
      .then((result) => {
        this.setState({ contentback: result.result });
      });
  };

  refreshPage() {
    setTimeout(() => {
      window.location.reload(false);
    }, 100);
    console.log("page to reload");
  }

  render() {
    let { content, next, previous } = this.state;
    console.log("next", next);
    console.log("previous", previous);
    return (
      <div>
        {/*Blog Details section*/}
        <section className="blog-detail-page section-b-space">
          <div className="container">
            <div className="row">
              <div className="col-sm-12 blog-detail">
                <div className="text-center">
                  <img src={`${content.image}`} className="img-fluid" alt="" />
                </div>
                <h3>{content.title}</h3>
                <ul className="post-social">
                  <li>{content.createdate}</li>
                </ul>
                <div class="row">
                  <div
                    class="col-12"
                    dangerouslySetInnerHTML={{ __html: content.description }}
                  />
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col">
                {previous != null && (
                  <Link
                    onClick={() => this.refreshPage()}
                    to={`${process.env.PUBLIC_URL}/blog/details/${previous.id}`}
                  >
                    <b>{"< บทความก่อนหน้า"}</b>
                  </Link>
                )}
              </div>
              <div className="col" style={{ textAlign: "right" }}>
                {next != null && (
                  <Link
                    onClick={() => this.refreshPage()}
                    to={`${process.env.PUBLIC_URL}/blog/details/${next.id}`}
                  >
                    <b>{"บทความถัดไป >"}</b>
                  </Link>
                )}
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  let contentId = ownProps.match.params.id;
  return {
    symbol: state.data.symbol,
    contentId: contentId,
  };
};

export default connect(mapStateToProps)(BlogDetails);
